body {
  background: #eee;
}
.container {
  max-width: 1399px;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
}
.inner {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.inner2 {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
.header {
  background: #fff;
  text-align: center;
  position: relative;
  border-top: 4px solid #b6a76d;
  border-bottom: 1px solid #eee;
}
.header .logo {
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
  margin-top: 22px;
  margin-bottom: 13px;
}
.header .language {
  color: #b6a788;
  font-family: simsun;
  position: absolute;
  right: 0;
  top: 20px;
}
.header .language a {
  color: #b6a788;
  font-family: "microsoft yahei";
}
.header .language .active {
  color: #b6a788;
}
.header .search {
  border: 1px solid #b6a788;
  position: absolute;
  right: 26px;
  top: 16px;
}
.header .search input {
  border: none;
  background: transparent;
  color: #252525;
  height: 18px;
  padding: 1px 10px;
  float: left;
  display: inline;
  width: 100px;
}
.header .search button {
  width: 20px;
  height: 20px;
  background: url(../img/ps1/ico-sch.png) no-repeat center center;
  border: none;
  cursor: pointer;
  float: left;
  display: inline;
}
.nav {
  position: absolute;
  z-index: 2;
  top: 60px;
}
.nav.l {
  left: 0;
}
.nav.r {
  right: 0;
}
.nav li {
  float: left;
  display: inline;
  position: relative;
}
.nav li a {
  display: block;
  height: 46px;
  line-height: 46px;
  text-align: center;
  width: 110px;
  color: #9a824f;
}
.nav li a:hover {
  background: #eaeaea;
}
.nav li ul {
  position: absolute;
  left: 0;
  top: 46px;
  right: 0;
  background: #fff;
  display: none;
  border: 1px solid #eee;
}
.nav li ul li {
  float: none;
  display: block;
}
.nav li ul li a:hover {
  background: #eaeaea;
}
.nav .active > a {
  background: #eaeaea;
}
.nav .active ul {
  display: block;
}
.nav .active ul a {
  width: 100%;
}
.footer {
  background: #fff;
  border-bottom: 5px solid #b6a76d;
  padding-top: 20px;
  border-top: 1px solid #ddd;
}
.footer .go-top {
  bottom: 100px;
  display: none;
  background: #fff;
  position: fixed;
  padding: 10px 0;
  text-align: center;
  width: 40px;
  border: 1px solid #ccc;
  /*border-radius: 50%;*/
  margin-left: 1202px;
}
.footer .go-top img {
  cursor: pointer;
}
.footer .inner {
  padding-top: 32px;
  padding-bottom: 22px;
}
.footer .link a {
  color: #a4894f;
  font-size: 16px;
  margin-right: 30px;
}
.footer .copy-right {
  position: absolute;
  left: 0;
  bottom: 22px;
  color: #979797;
}
.footer .qr-code {
  float: right;
  display: inline;
}
.footer .qr-code b {
  display: block;
  text-align: center;
  padding-top: 10px;
  font-weight: normal;
}
.btn.theme1 {
  border: 1px solid #b6a76d;
  color: #b6a76d;
  display: inline-block;
  padding: 10px 40px;
}
.btn.theme1:hover {
  background: #b6a76d;
