  height: 584px;
  background: url(../img/ps3/h7.png) no-repeat center center;
  background-size: cover;
}
.core7.x7 {
  background: url(../img/ps3/x6.jpg) no-repeat center center;
  background-size: cover;
}
.core7.z6 {
  background: url(../img/ps3/z5.jpg) no-repeat center center;
  background-size: cover;
}
.core7.v6 {
  height: 578px;
  background: url(../img/ps3/v5.jpg) no-repeat center center;
  background-size: cover;
}
.core7 .core7B {
  padding-top: 200px;
}
.core7 .core7B img {
  float: left;
  margin-left: 20px;
}
.core7 .core7B .text {
  float: left;
  margin-left: 34px;
  margin-top: 42px;
}
.core7 .core7B .text h3 {
  font-size: 24px;
  margin-bottom: 15px;
}
.core7 .core7B .text .a1 {
  font-size: 24px;
  margin-bottom: 16px;
}
.core7 .core7B .text .a2 {
  font-size: 20px;
}
.core7 .core7B .text .a2 span {
  display: block;
  font-size: 18px;
  margin-top: 8px;
}
.focus-shzr .prev.self {
